import { TooptipsPopup } from "../Tooptips";
/**
 * 表单验证错误处理器
 * @param error
 * @param data
 */
export function formValidateFailHandle(error, data) {
    if (error.input) {
        error.input.scrollIntoView({ behavior: "smooth", block: "nearest", inline: "nearest" });
    }
    TooptipsPopup({ children: error.message });
}
